/* Mobile-only sidebar styles */
.mobile-only {
  display: none;
}

@media (max-width: 768px) {
  .mobile-only {
    display: block;
  }
  
  .desktop-nav {
    display: none;
  }
  
  .sidebar-toggle {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media (min-width: 769px) {
  .sidebar-toggle {
    display: none;
  }
}

/* Modern Sidebar Styles */
.sidebar-toggle {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border: none;
  color: white;
  padding: 0.2rem;
  border-radius: 0;
  cursor: pointer;
  font-size: 0.7rem;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
  width: 24px;
  height: 24px;
}

.sidebar-toggle:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.sidebar {
  position: fixed;
  top: 0;
  left: -240px;
  width: 240px;
  height: 100vh;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.9));
  backdrop-filter: blur(20px);
  box-shadow: 10px 0 30px rgba(0, 0, 0, 0.1);
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: 1000;
  border-right: 1px solid rgba(102, 126, 234, 0.2);
}

.sidebar.active {
  left: 0;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.sidebar-header h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0;
}

.close-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  padding: 0.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(90deg);
}

.sidebar-menu {
  padding: 2rem 0;
}

.menu-item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 0.8rem 1.5rem;
  color: #333;
  text-decoration: none;
  transition: all 0.3s ease;
  border: none;
  background: none;
  width: 100%;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  position: relative;
}

.menu-item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(135deg, #667eea, #764ba2);
  transform: scaleY(0);
  transition: transform 0.3s ease;
}

.menu-item:hover::before {
  transform: scaleY(1);
}

.menu-item:hover {
  background: rgba(102, 126, 234, 0.1);
  color: #667eea;
  padding-left: 2.5rem;
}

.menu-item svg {
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.menu-item:hover svg {
  transform: scale(1.2);
  color: #667eea;
}

.logout-btn {
  margin-top: 2rem;
  border-top: 1px solid rgba(102, 126, 234, 0.2);
  padding-top: 2rem !important;
}

.logout-btn:hover {
  background: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
}

.logout-btn:hover svg {
  color: #e74c3c;
}

.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  z-index: 999;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .sidebar {
    width: 240px;
    left: -240px;
  }
  
  .sidebar-header {
    padding: 1rem;
  }
  
  .menu-item {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
  }
}

/* Animation for menu items */
.sidebar.active .menu-item {
  animation: slideInRight 0.4s ease forwards;
  opacity: 0;
}

.sidebar.active .menu-item:nth-child(1) { animation-delay: 0.1s; }
.sidebar.active .menu-item:nth-child(2) { animation-delay: 0.2s; }
.sidebar.active .menu-item:nth-child(3) { animation-delay: 0.3s; }
.sidebar.active .menu-item:nth-child(4) { animation-delay: 0.4s; }
.sidebar.active .menu-item:nth-child(5) { animation-delay: 0.5s; }

@keyframes slideInRight {
  from {
    transform: translateX(50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}