@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  --primary-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --secondary-gradient: linear-gradient(135deg, #3498db 0%, #2980b9 100%);
  --success-gradient: linear-gradient(135deg, #27ae60 0%, #229954 100%);
  --danger-gradient: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  --warning-gradient: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  --dark-gradient: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  --teal-gradient: linear-gradient(135deg, #1abc9c 0%, #16a085 100%);
  --glass-bg: rgba(255, 255, 255, 0.1);
  --glass-border: rgba(255, 255, 255, 0.2);
  --shadow-light: 0 8px 32px rgba(0, 0, 0, 0.1);
  --shadow-medium: 0 12px 40px rgba(0, 0, 0, 0.15);
  --shadow-heavy: 0 20px 60px rgba(0, 0, 0, 0.2);
}

.navbar {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: var(--shadow-light);
  position: sticky;
  top: 0;
  z-index: 100;
  border-bottom: 1px solid var(--glass-border);
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.nav-brand .logo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.nav-brand h1 {
  background: linear-gradient(45deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  font-size: 1.8rem;
  margin-top: 10px;
}

.nav-links {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

@media (max-width: 768px) {
  .nav-links {
    flex-direction: column;
    gap: 0.5rem;
  }
}

.nav-links a {
  color: #333;
  text-decoration: none;
  padding: 0.7rem 1.5rem;
  border-radius: 25px;
  transition: all 0.3s ease;
  font-weight: 500;
}

.nav-links a:hover {
  background: var(--primary-gradient);
  color: white;
  transform: translateY(-2px);
  box-shadow: var(--shadow-light);
}

.nav-links button {
  background: var(--danger-gradient);
  border: none;
  color: white;
  padding: 0.7rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
}

.nav-links button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 107, 107, 0.4);
}

.home {
  padding: 0;
}

.hero {
  text-align: center;
  padding: 4rem 2rem;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.95), rgba(118, 75, 162, 0.95));
  color: white;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--glass-border);
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="25" cy="25" r="1" fill="white" opacity="0.1"/><circle cx="75" cy="75" r="1" fill="white" opacity="0.1"/><circle cx="50" cy="10" r="0.5" fill="white" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  z-index: 1;
}

.hero::after {
  content: 'function learn() { return "success"; } \A if(student.motivated) { kalvizhi.teach(); } \A <html><body>Hello World</body></html> \A print("Welcome to Kalvizhi")';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-family: 'Courier New', monospace;
  font-size: 0.7rem;
  color: rgba(255,255,255,0.05);
  white-space: pre;
  line-height: 3;
  z-index: 1;
  animation: codeFlow 30s linear infinite;
  pointer-events: none;
}

@keyframes codeFlow {
  0% { transform: translateY(100%); }
  100% { transform: translateY(-100%); }
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  margin: 0 auto;
}

.hero h2 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.hero .tagline {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.hero .description {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 2rem;
  opacity: 0.8;
}

.cta-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-button {
  padding: 1rem 2rem;
  border: none;
  border-radius: 30px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
}

.cta-primary {
  background: var(--danger-gradient);
  color: white;
  box-shadow: var(--shadow-medium);
}

.cta-secondary {
  background: var(--glass-bg);
  color: white;
  border: 2px solid var(--glass-border);
  backdrop-filter: blur(10px);
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.features-section {
  padding: 4rem 2rem;
  background: white;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  background: linear-gradient(45deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.feature-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  padding: 2.5rem;
  border-radius: 25px;
  box-shadow: var(--shadow-light);
  transition: all 0.4s ease;
  border: 1px solid var(--glass-border);
  position: relative;
  overflow: hidden;
}

.feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.1), transparent);
  transition: left 0.6s;
}

.feature-card:hover::before {
  left: 100%;
}

.feature-card:hover {
  transform: translateY(-15px) scale(1.02);
  box-shadow: var(--shadow-heavy);
  border-color: rgba(102, 126, 234, 0.4);
  background: rgba(255, 255, 255, 0.98);
}

.feature-icon {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  display: block;
  animation: iconBounce 2s ease-in-out infinite;
  filter: drop-shadow(0 10px 20px rgba(0,0,0,0.2));
  transition: all 0.3s ease;
}

.feature-card:hover .feature-icon {
  transform: scale(1.2) rotate(10deg);
  filter: drop-shadow(0 15px 30px rgba(102, 126, 234, 0.4));
}

@keyframes iconBounce {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.feature-card h3 {
  color: #333;
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.feature-card p {
  color: #666;
  line-height: 1.6;
}

.courses-section {
  padding: 4rem 2rem;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.9) 0%, rgba(118, 75, 162, 0.9) 100%);
  backdrop-filter: blur(10px);
  position: relative;
}

.courses-section .section-title {
  color: white !important;
  background: none !important;
  -webkit-text-fill-color: white !important;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: 2.5rem;
  margin-top: 2rem;
}

.dashboard .courses-grid {
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
}

.course-card {
  background: rgba(255, 255, 255, 0.95);
  padding: 2.5rem;
  border-radius: 25px;
  box-shadow: var(--shadow-medium);
  transition: all 0.4s ease;
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  position: relative;
  overflow: hidden;
}

.course-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(45deg, #667eea, #764ba2);
}

.course-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: var(--shadow-heavy);
  background: rgba(255, 255, 255, 0.98);
}

.course-card h4 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.course-card p {
  color: #666;
  line-height: 1.7;
  margin-bottom: 2rem;
  font-size: 1rem;
}

.auth-form {
  max-width: 450px;
  margin: 3rem auto;
  padding: 3rem;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 25px;
  box-shadow: var(--shadow-medium);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
}

.auth-form h2 {
  text-align: center;
  margin-bottom: 2rem;
  background: linear-gradient(45deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #333;
}

.form-group input, .form-group select {
  width: 100%;
  padding: 1rem;
  border: 2px solid #e1e8ed;
  border-radius: 15px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
}

.form-group input:focus, .form-group select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

button {
  background: var(--primary-gradient);
  color: white;
  padding: 1rem 2rem;
  border: none;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-light);
}

button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

button:hover::before {
  left: 100%;
}

button:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-medium);
}

.dashboard button {
  padding: 0.8rem 1.5rem;
  font-size: 0.95rem;
}

.message {
  padding: 1rem;
  margin-bottom: 1rem;
  background: linear-gradient(45deg, #00b894, #00cec9);
  color: white;
  border-radius: 15px;
  text-align: center;
  font-weight: 500;
}

.error {
  padding: 1rem;
  margin-top: 1rem;
  background: linear-gradient(45deg, #ff6b6b, #ee5a24);
  color: white;
  border-radius: 15px;
  text-align: center;
  font-weight: 500;
}

.dashboard {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.08), rgba(118, 75, 162, 0.08));
  min-height: 100vh;
  backdrop-filter: blur(5px);
}

.user-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 25px;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--glass-border);
}

.user-info h2 {
  font-size: 2.5rem;
  background: linear-gradient(45deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.user-details {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.username {
  background: white;
  color: #333;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.9rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #e1e8ed;
}

.user-email {
  color: #666;
  font-size: 0.9rem;
}

.eligibility-badge {
  display: flex;
  align-items: center;
}

.badge-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.eligibility-badge .badge {
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

.refresh-btn {
  padding: 0.4rem 0.8rem !important;
  border-radius: 15px !important;
  border: none !important;
  background: #667eea !important;
  color: white !important;
  cursor: pointer !important;
  font-size: 0.8rem !important;
  font-weight: 500 !important;
  transition: all 0.3s ease !important;
  width: auto !important;
}

.refresh-btn:hover {
  background: #5a6fd8 !important;
  transform: translateY(-1px) !important;
}

.badge.eligible {
  background: #27ae60;
  color: white;
}

.badge.not_eligible {
  background: #e74c3c;
  color: white;
}

.badge.pending_review {
  background: #f39c12;
  color: white;
}

.hero-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.hero-logo-img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
}

.course-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
  border-radius: 20px 20px 0 0;
  position: relative;
}

.course-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s ease;
  animation: imageFloat 6s ease-in-out infinite;
}

.course-card:hover .course-image img {
  transform: scale(1.1) rotate(2deg);
  filter: brightness(1.2) saturate(1.3);
}

.course-image::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.4), transparent);
  animation: imageShimmer 3s ease-in-out infinite;
  z-index: 1;
}

@keyframes imageFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-5px); }
}

@keyframes imageShimmer {
  0% { left: -100%; }
  50% { left: 100%; }
  100% { left: 100%; }
}

.course-content {
  padding: 1.5rem;
}

.course-link {
  display: inline-block;
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  padding: 0.8rem 1.5rem;
  text-decoration: none;
  border-radius: 25px;
  margin-top: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 0.9rem;
}

.course-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.4);
  color: white;
}

.section-animation {
  position: absolute;
  top: 2rem;
  right: 2rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  opacity: 0.1;
  animation: codeGlow 3s ease-in-out infinite;
}

.code-snippet {
  color: #667eea;
}

.html-snippet {
  color: #ff6b6b;
}

.python-snippet {
  color: #00ff00;
}

.code-keyword {
  color: #ff79c6;
  font-weight: bold;
}

.code-variable {
  color: #50fa7b;
}

.code-function {
  color: #8be9fd;
}

.html-tag {
  color: #ff5555;
}

@keyframes codeGlow {
  0%, 100% { opacity: 0.1; transform: scale(1); }
  50% { opacity: 0.3; transform: scale(1.05); }
}

.features-section, .courses-section {
  position: relative;
}

.section {
  margin-bottom: 3rem;
  background: rgba(255, 255, 255, 0.95);
  padding: 2.5rem;
  border-radius: 30px;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(20px);
  transition: all 0.3s ease;
}

.section:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-medium);
  background: rgba(255, 255, 255, 0.98);
}

.section h3 {
  margin-bottom: 2rem;
  color: #2c3e50;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.application-item {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 2.5rem;
  margin-bottom: 1.5rem;
  border-radius: 25px;
  box-shadow: 0 15px 40px rgba(102, 126, 234, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.application-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(255,255,255,0.1), transparent);
  pointer-events: none;
}

.application-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 25px 60px rgba(102, 126, 234, 0.3);
}

.application-item h4 {
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.status {
  font-weight: 700;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.9rem;
}

.status.pending {
  background: #f39c12;
  color: white;
}

.status.approved {
  background: #27ae60;
  color: white;
}

.status.rejected {
  background: #e74c3c;
  color: white;
}

.whatsapp-group a {
  display: inline-block;
  background: #25d366;
  color: white;
  padding: 0.8rem 1.5rem;
  text-decoration: none;
  border-radius: 25px;
  margin-top: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.whatsapp-group a:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(37, 211, 102, 0.4);
}

.footer {
  background: #2c3e50;
  color: white;
  text-align: center;
  padding: 2rem;
  margin-top: 4rem;
}

.footer p {
  margin-bottom: 0.5rem;
}

.powered-by {
  font-size: 0.9rem;
  opacity: 0.8;
}

.coding-animation {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
}

.code-window {
  background: #1e1e1e;
  border-radius: 10px;
  width: 300px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.3);
  overflow: hidden;
}

.window-header {
  background: #333;
  padding: 0.8rem;
  display: flex;
  gap: 0.5rem;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.dot.red { background: #ff5f56; }
.dot.yellow { background: #ffbd2e; }
.dot.green { background: #27ca3f; }

.code-content {
  padding: 1.5rem;
  font-family: 'Courier New', monospace;
  color: #00ff00;
  font-size: 1rem;
}

.typing-text {
  border-right: 2px solid #00ff00;
  animation: typing 3s steps(20) infinite, blink 1s infinite;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}

@keyframes typing {
  0% { width: 0; }
  50% { width: 100%; }
  100% { width: 0; }
}

@keyframes blink {
  0%, 50% { border-color: #00ff00; }
  51%, 100% { border-color: transparent; }
}

.floating-code {
  position: absolute;
  font-family: 'Courier New', monospace;
  color: rgba(255,255,255,0.1);
  font-size: 0.8rem;
  animation: float 20s linear infinite;
  pointer-events: none;
}

.floating-code:nth-child(1) { top: 10%; left: 10%; animation-delay: 0s; }
.floating-code:nth-child(2) { top: 20%; right: 15%; animation-delay: 5s; }
.floating-code:nth-child(3) { bottom: 30%; left: 20%; animation-delay: 10s; }
.floating-code:nth-child(4) { bottom: 20%; right: 10%; animation-delay: 15s; }

@keyframes float {
  0% { transform: translateY(0px) rotate(0deg); opacity: 0; }
  10% { opacity: 0.1; }
  90% { opacity: 0.1; }
  100% { transform: translateY(-100px) rotate(360deg); opacity: 0; }
}

.features-coding-animation {
  display: flex;
  justify-content: center;
  margin: 3rem 0;
}

.feature-code-window {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border-radius: 20px;
  width: 450px;
  box-shadow: 0 20px 60px rgba(0,0,0,0.5);
  overflow: hidden;
  transform: perspective(1000px) rotateX(5deg);
  transition: all 0.4s ease;
  border: 1px solid rgba(102, 126, 234, 0.3);
}

.feature-code-window:hover {
  transform: perspective(1000px) rotateX(0deg) scale(1.05);
  box-shadow: 0 30px 80px rgba(102, 126, 234, 0.4);
}

.feature-window-header {
  background: linear-gradient(135deg, #667eea, #764ba2);
  padding: 1.2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.feature-window-title {
  color: white;
  font-size: 1rem;
  margin-left: auto;
  font-family: 'Courier New', monospace;
  font-weight: 600;
  text-shadow: 0 0 10px rgba(255,255,255,0.5);
}

.feature-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  animation: featurePulse 3s infinite;
  box-shadow: 0 0 15px rgba(255,255,255,0.4);
}

.feature-dot.red { background: #ff5f56; animation-delay: 0s; }
.feature-dot.yellow { background: #ffbd2e; animation-delay: 0.5s; }
.feature-dot.green { background: #27ca3f; animation-delay: 1s; }

.feature-code-content {
  padding: 2.5rem;
  font-family: 'Courier New', monospace;
  font-size: 1.2rem;
  line-height: 2;
  background: linear-gradient(135deg, #1a1a2e, #0f0f23);
}

.feature-code-line {
  color: #61dafb;
  margin-bottom: 0.8rem;
  opacity: 0;
  animation: featureFadeIn 0.8s ease forwards;
}

.feature-code-line:nth-child(1) { animation-delay: 0.5s; color: #ff79c6; }
.feature-code-line:nth-child(2) { animation-delay: 1.5s; }
.feature-code-line:nth-child(3) { animation-delay: 2.5s; }
.feature-code-line:nth-child(4) { animation-delay: 3.5s; }
.feature-code-line:nth-child(5) { animation-delay: 4.5s; color: #ff79c6; }

.feature-typing-1 {
  border-right: 3px solid #00ff00;
  animation: featureTyping1 3s steps(20) infinite, featureGlow 1s infinite;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
  animation-delay: 1.5s;
  text-shadow: 0 0 10px #00ff00;
  color: #00ff00 !important;
}

.feature-typing-2 {
  border-right: 3px solid #ffbd2e;
  animation: featureTyping2 3s steps(25) infinite, featureGlow2 1s infinite;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
  animation-delay: 2.5s;
  text-shadow: 0 0 10px #ffbd2e;
  color: #ffbd2e !important;
}

.feature-typing-3 {
  border-right: 3px solid #ff6b6b;
  animation: featureTyping3 3s steps(18) infinite, featureGlow3 1s infinite;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
  animation-delay: 3.5s;
  text-shadow: 0 0 10px #ff6b6b;
  color: #ff6b6b !important;
}

@keyframes featurePulse {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.7; transform: scale(1.3); }
}

@keyframes featureFadeIn {
  0% { opacity: 0; transform: translateX(-30px); }
  100% { opacity: 1; transform: translateX(0); }
}

@keyframes featureTyping1 {
  0% { width: 0; }
  40% { width: 100%; }
  60% { width: 100%; }
  100% { width: 0; }
}

@keyframes featureTyping2 {
  0% { width: 0; }
  45% { width: 100%; }
  65% { width: 100%; }
  100% { width: 0; }
}

@keyframes featureTyping3 {
  0% { width: 0; }
  50% { width: 100%; }
  70% { width: 100%; }
  100% { width: 0; }
}

@keyframes featureGlow {
  0%, 50% { border-color: #00ff00; box-shadow: 0 0 10px #00ff00; }
  51%, 100% { border-color: transparent; box-shadow: none; }
}

@keyframes featureGlow2 {
  0%, 50% { border-color: #ffbd2e; box-shadow: 0 0 10px #ffbd2e; }
  51%, 100% { border-color: transparent; box-shadow: none; }
}

@keyframes featureGlow3 {
  0%, 50% { border-color: #ff6b6b; box-shadow: 0 0 10px #ff6b6b; }
  51%, 100% { border-color: transparent; box-shadow: none; }
}

.learning-path-title {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 4rem;
  text-shadow: 0 0 20px rgba(255,255,255,0.5);
  animation: titlePulse 3s ease-in-out infinite;
}

.learning-path-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  position: relative;
}

.path-step {
  background: rgba(255,255,255,0.15);
  padding: 2.5rem;
  border-radius: 25px;
  width: 280px;
  position: relative;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255,255,255,0.2);
  transition: all 0.5s ease;
  animation: stepFloat 4s ease-in-out infinite;
}

.path-step:hover {
  transform: scale(1.1) translateY(-10px);
  background: rgba(255,255,255,0.25);
  box-shadow: 0 20px 40px rgba(0,0,0,0.3);
}

.step-1 { animation-delay: 0s; }
.step-2 { animation-delay: 1s; }
.step-3 { animation-delay: 2s; }
.step-4 { animation-delay: 3s; }

.step-icon {
  font-size: 4rem;
  text-align: center;
  margin-bottom: 1rem;
  animation: iconSpin 6s linear infinite;
  filter: drop-shadow(0 0 15px rgba(255,255,255,0.8));
}

.step-content h4 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
}

.step-content p {
  text-align: center;
  line-height: 1.6;
  opacity: 0.9;
}

.step-number {
  position: absolute;
  top: -15px;
  right: -15px;
  background: linear-gradient(45deg, #ff6b6b, #ee5a24);
  color: white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 900;
  font-size: 1.2rem;
  box-shadow: 0 5px 15px rgba(255,107,107,0.4);
  animation: numberPulse 2s ease-in-out infinite;
}

.path-connector {
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  border-radius: 2px;
  position: relative;
  animation: connectorGlow 3s ease-in-out infinite;
}

.path-connector::before {
  content: '';
  position: absolute;
  top: -2px;
  left: 0;
  width: 100%;
  height: 8px;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.6), transparent);
  border-radius: 4px;
  animation: connectorFlow 2s linear infinite;
}

@keyframes titlePulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes stepFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
}

@keyframes iconSpin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes numberPulse {
  0%, 100% { transform: scale(1); box-shadow: 0 5px 15px rgba(255,107,107,0.4); }
  50% { transform: scale(1.2); box-shadow: 0 10px 25px rgba(255,107,107,0.8); }
}

@keyframes connectorGlow {
  0%, 100% { box-shadow: 0 0 10px rgba(102, 126, 234, 0.5); }
  50% { box-shadow: 0 0 20px rgba(102, 126, 234, 1); }
}

@keyframes connectorFlow {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.step-code-animation {
  margin: 1rem 0;
}

.mini-code-window {
  background: #1a1a1a;
  border-radius: 8px;
  padding: 0.8rem;
  border: 1px solid rgba(255,255,255,0.2);
}

.mini-code-content {
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
}

.mini-typing-text {
  border-right: 2px solid currentColor;
  animation: miniTyping 3s steps(20) infinite, miniBlink 1s infinite;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}

.path-step.approved {
  background: rgba(39, 174, 96, 0.2);
  border-color: #27ae60;
}

.path-step.pending {
  background: rgba(243, 156, 18, 0.2);
  border-color: #f39c12;
}

.path-step.rejected {
  background: rgba(231, 76, 60, 0.2);
  border-color: #e74c3c;
}

@keyframes miniTyping {
  0% { width: 0; }
  50% { width: 100%; }
  100% { width: 0; }
}

@keyframes miniBlink {
  0%, 50% { border-color: currentColor; }
  51%, 100% { border-color: transparent; }
}

