}

.status-badge.approved {
  background: #28a745;
  color: white;
}

.status-badge.rejected {
  background: #dc3545;
  color: white;
}

.status-badge.eligible {
  background: #28a745;
  color: white;
}

.status-badge.not_eligible {
  background: #dc3545;
  color: white;
}

.status-badge.pending_review {
  background: #ffc107;
  color: #212529;
}

/* Responsive Design for Bulk Actions */
@media (max-width: 768px) {
  .bulk-actions-bar {
    flex-direction: column;
    gap: 15px;
    align-items: stretch;
  }
  
  .bulk-buttons {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .admin-menu-bar {
    flex-direction: column;
    gap: 15px;
  }
  
  .admin-tabs {
    flex-direction: column;
    width: 100%;
  }
  
  .admin-tab {
    width: 100%;
    justify-content: center;
  }
  
  /* Hide desktop tabs on mobile */
  .admin-dashboard > .admin-tabs {
    display: none;
  }
}
/* Enhanced Hover Effects */
.stat-card:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: var(--shadow-medium);
}

.admin-tab:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-light);
}

.add-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-light);
}

.edit-btn:hover, .delete-btn:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-light);
}



.admin-section {
  background: rgba(255,255,255,0.95);
  backdrop-filter: blur(20px);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--glass-border);
}

.form-modal {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);
}

.form-content {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  box-shadow: var(--shadow-heavy);
}

/* Enhanced Animations */
@keyframes slideInUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInScale {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.admin-header {
  animation: slideInUp 0.6s ease;
}

.admin-section {
  animation: fadeInScale 0.5s ease;
}

.stat-card {
  animation: slideInUp 0.4s ease;
}

.stat-card:nth-child(1) { animation-delay: 0.1s; }
.stat-card:nth-child(2) { animation-delay: 0.2s; }
.stat-card:nth-child(3) { animation-delay: 0.3s; }

/* Improved Status Badges */
.status-badge {
  position: relative;
  overflow: hidden;
}

.status-badge::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  transition: left 0.5s;
}

.status-badge:hover::before {
  left: 100%;
}

/* Enhanced Table Styling */
.admin-table {
  border-radius: 12px;
  overflow: hidden;
  box-shadow: var(--shadow-light);
}

.admin-table tbody tr {
  transition: all 0.2s ease;
}

.admin-table tbody tr:hover {
  background: rgba(102, 126, 234, 0.05);
  transform: scale(1.01);
}

/* Improved Bulk Actions */
.bulk-actions-bar {
  background: var(--teal-gradient);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.1);
}

/* Enhanced Form Elements */
.form-group input:focus, .form-group select:focus {
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
  transform: translateY(-1px);
}

/* Improved Course Links */
.course-link {
  background: var(--primary-gradient);
  box-shadow: var(--shadow-light);
}

.course-link:hover {
  box-shadow: var(--shadow-medium);
}

/* Enhanced Application Items */
.application-item {
  background: var(--primary-gradient);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.1);
}

/* Improved WhatsApp Links */
.whatsapp-group a {
  background: var(--success-gradient);
  box-shadow: var(--shadow-light);
}

.whatsapp-group a:hover {
  box-shadow: var(--shadow-medium);
}

/* Enhanced Messages */
.message {
  background: var(--success-gradient);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.2);
}

.error {
  background: var(--danger-gradient);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.2);
}

/* Improved Refresh Button */
.refresh-all-btn {
  background: var(--secondary-gradient);
  box-shadow: var(--shadow-light);
}

.refresh-all-btn:hover {
  box-shadow: var(--shadow-medium);
}

/* Enhanced Mobile Responsiveness */
@media (max-width: 768px) {
  .admin-header {
    padding: 1.5rem;
  }
  
  .admin-section {
    padding: 1.5rem;
  }
  
  .stat-card {
    padding: 1rem;
  }
}


/* Profile Page Styles */
.profile-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.profile-form .form-group {
  margin-bottom: 1rem;
}

.profile-form input:disabled {
  background: #f8f9fa;
  color: #666;
  cursor: not-allowed;
}

.profile-actions {
  grid-column: 1 / -1;
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.profile-actions button {
  width: auto;
  padding: 0.8rem 2rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin: 2rem 0;
}

.stat-item {
  background: var(--secondary-gradient);
  color: white;
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  box-shadow: var(--shadow-light);
  transition: all 0.3s ease;
}

.stat-item:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-medium);
}

.stat-item .stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.stat-item .stat-label {
  font-size: 0.9rem;
  opacity: 0.9;
}

.course-history {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.history-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 12px;
  border: 1px solid var(--glass-border);
  transition: all 0.3s ease;
}

.history-item:hover {
  background: rgba(255, 255, 255, 0.95);
  transform: translateX(5px);
}

.course-info h4 {
  margin-bottom: 0.5rem;
  color: #2c3e50;
}

.course-info p {
  color: #666;
  font-size: 0.9rem;
}

.email-btn {
  background: linear-gradient(135deg, #9b59b6, #8e44ad);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.email-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(155, 89, 182, 0.3);
}

.email-modal {
  width: 600px;
  max-width: 95vw;
}

.email-target {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
  padding: 15px;
  background: rgba(102, 126, 234, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(102, 126, 234, 0.2);
}

.email-target label {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  font-weight: 500;
  color: #333;
}

.email-target input[type="radio"] {
  width: 16px;
  height: 16px;
  accent-color: #667eea;
}

.email-modal textarea {
  min-height: 120px;
  font-family: inherit;
  resize: vertical;
}

.form-actions button:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
}

.form-actions button:disabled:hover {
  transform: none;
  box-shadow: none;
}

@media (max-width: 768px) {
  .email-modal {
    width: 95vw;
  }
  
  .email-target {
    padding: 10px;
  }
}